/*! Headings - <h1> through </h6> */
h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: "Lora", serif;
	font-weight: normal;
}

/*! Body Typography */

body {
	font-family: "Noto Sans", sans-serif;
	font-weight: normal;
}

.wp-admin.gutenberg-editor-page {
	.wp-block-button {
		.wp-block-button__link {
			border: 0 none;
			border-radius: 3px;
			font-size: 14px;
			font-weight: 700;
			height: 48px;
			letter-spacing: 0.6px;
			line-height: 1.25;
			padding: 15px 40px;
			text-transform: uppercase;
		}
	}
}
